<script setup lang="ts">
import { ref, onMounted, onUnmounted } from 'vue'
import { useNotificationStore } from '@/stores/notifications'
import { websocketManager, useWebSocket } from '@/services/websocket'
import DashboardHeader from '@/components/layout/DashboardHeader.vue'
import { EyeIcon, XMarkIcon, CheckIcon } from '@heroicons/vue/24/outline'

const notifications = useNotificationStore()
const { connect, isConnected } = useWebSocket()

// ÂìçÂ∫îÂºèÊï∞ÊçÆ
const selectedSessionId = ref('')
const contextData = ref(null)
const loading = ref(false)
const error = ref('')
const activeTab = ref('preprocessing')
const showFeaturePreview = ref(false)

// Ê†áÁ≠æÈ°µÂÆö‰πâ
const tabs = [
  { id: 'preprocessing', name: 'È¢ÑÂ§ÑÁêÜ' },
  { id: 'retrieval', name: 'ËÆ∞ÂøÜÊ£ÄÁ¥¢' }, 
  { id: 'history', name: 'ÂéÜÂè≤ËÅöÂêà' },
  { id: 'final', name: 'ÊúÄÁªà‰∏ä‰∏ãÊñá' }
]

// APIÂü∫Á°ÄURL
const API_BASE = 'http://localhost:5000/api'

// Â∑•ÂÖ∑ÂáΩÊï∞
const formatDuration = (duration: number | null | undefined): string => {
  if (!duration && duration !== 0) return 'Êú™Áü•'
  if (duration < 0.001) return '<1ms'
  if (duration < 1) return `${(duration * 1000).toFixed(1)}ms`
  return `${duration.toFixed(3)}s`
}

// Âä†ËΩΩ‰ºöËØù‰∏ä‰∏ãÊñáÊï∞ÊçÆ
const loadContextData = async () => {
  if (!selectedSessionId.value.trim()) {
    error.value = 'ËØ∑ËæìÂÖ•‰ºöËØùID'
    return
  }

  loading.value = true
  error.value = ''
  
  try {
    const response = await fetch(`${API_BASE}/session/${selectedSessionId.value}/context`)
    
    if (!response.ok) {
      const errorData = await response.json()
      throw new Error(errorData.error || `HTTP ${response.status}`)
    }
    
    const data = await response.json()
    contextData.value = data
    
    notifications.add({
      type: 'success',
      title: '‰∏ä‰∏ãÊñáÂä†ËΩΩÊàêÂäü',
      message: `Â∑≤Âä†ËΩΩ‰ºöËØù ${selectedSessionId.value} ÁöÑ‰∏ä‰∏ãÊñáÊï∞ÊçÆ`
    })
    
  } catch (err: any) {
    error.value = err.message || 'Âä†ËΩΩÂ§±Ë¥•'
    contextData.value = null
    
    notifications.add({
      type: 'error', 
      title: 'Âä†ËΩΩÂ§±Ë¥•',
      message: error.value
    })
  } finally {
    loading.value = false
  }
}

// Âä†ËΩΩÂΩìÂâçÂÆûÊó∂‰∏ä‰∏ãÊñá
const loadCurrentContext = async () => {
  loading.value = true
  error.value = ''
  
  try {
    // ‰ºòÂÖàÂ∞ùËØïWebSocketÂÆûÊó∂Ëé∑Âèñ
    if (isConnected()) {
      // ËÆ¢ÈòÖ‰∏ä‰∏ãÊñáÊõ¥Êñ∞
      websocketManager.subscribeContext()
      
      // ËÆæÁΩÆWebSocketÁõëÂê¨Âô®
      websocketManager.on('context_status_update', handleContextWebSocketUpdate)
      
      notifications.add({
        type: 'info',
        title: 'Â∑≤ÂêØÁî®ÂÆûÊó∂‰∏ä‰∏ãÊñáÁõëÊéß',
        message: 'ÈÄöËøáWebSocketËé∑ÂèñÂÆûÊó∂‰∏ä‰∏ãÊñáÊõ¥Êñ∞'
      })
      
      loading.value = false
      return
    }
    
    // ÈôçÁ∫ßÂà∞HTTP API
    const response = await fetch(`${API_BASE}/current_context`)
    
    if (!response.ok) {
      const errorData = await response.json()
      throw new Error(errorData.error || `HTTP ${response.status}`)
    }
    
    const data = await response.json()
    
    if (!data.active) {
      // Ê≤°ÊúâÊ¥ªË∑É‰ºöËØùÔºåÊòæÁ§∫ÊèêÁ§∫
      notifications.add({
        type: 'info',
        title: 'ÊöÇÊó†Ê¥ªË∑É‰∏ä‰∏ãÊñá',
        message: data.message || 'ÂΩìÂâçÊ≤°ÊúâÊ≠£Âú®Â§ÑÁêÜÁöÑ‰∏ä‰∏ãÊñáÊûÑÂª∫ËøáÁ®ã'
      })
      contextData.value = null
      return
    }
    
    // ÊúâÊ¥ªË∑É‰ºöËØùÔºåÊòæÁ§∫ÈÉ®ÂàÜ‰∏ä‰∏ãÊñá
    contextData.value = {
      session_id: data.session_id,
      timestamp: data.timestamp,
      preprocessing: data.partial_context?.preprocessing || {},
      memory_retrieval: data.partial_context?.memory_retrieval || {},
      history_aggregation: {},
      final_context: {}
    }
    
    selectedSessionId.value = data.session_id
    
    notifications.add({
      type: 'success',
      title: 'ÂÆûÊó∂‰∏ä‰∏ãÊñáÂä†ËΩΩÊàêÂäü',
      message: `ÂΩìÂâçÊ≠£Âú®Â§ÑÁêÜ‰ºöËØù: ${data.session_id}`
    })
    
  } catch (err: any) {
    error.value = err.message || 'Âä†ËΩΩÂÆûÊó∂‰∏ä‰∏ãÊñáÂ§±Ë¥•'
    contextData.value = null
    
    notifications.add({
      type: 'error',
      title: 'Âä†ËΩΩÂ§±Ë¥•', 
      message: error.value
    })
  } finally {
    loading.value = false
  }
}

// WebSocket‰∏ä‰∏ãÊñáÊõ¥Êñ∞Â§ÑÁêÜÂô®
const handleContextWebSocketUpdate = (data: any) => {
  console.log('üìù Êî∂Âà∞WebSocket‰∏ä‰∏ãÊñáÊõ¥Êñ∞:', data)
  
  if (!data.active) {
    contextData.value = null
    notifications.add({
      type: 'info',
      title: '‰∏ä‰∏ãÊñáÁä∂ÊÄÅÊõ¥Êñ∞',
      message: data.message || 'ÂΩìÂâçÊ≤°ÊúâÊ¥ªË∑ÉÁöÑ‰∏ä‰∏ãÊñáÊûÑÂª∫ËøáÁ®ã'
    })
    return
  }
  
  // Êõ¥Êñ∞‰∏ä‰∏ãÊñáÊï∞ÊçÆ
  contextData.value = {
    session_id: data.session_id,
    timestamp: data.timestamp,
    preprocessing: data.partial_context?.preprocessing || {},
    memory_retrieval: data.partial_context?.memory_retrieval || {},
    history_aggregation: {},
    final_context: {}
  }
  
  selectedSessionId.value = data.session_id
}

// ÁªÑ‰ª∂ÊåÇËΩΩÊó∂ÁöÑÂàùÂßãÂåñ
onMounted(async () => {
  console.log('ContextViewer mounted')
  
  // Â∞ùËØïËøûÊé•WebSocket
  try {
    await connect()
    console.log('‚úÖ ContextViewer WebSocketËøûÊé•ÊàêÂäü')
  } catch (error) {
    console.warn('‚ö†Ô∏è ContextViewer WebSocketËøûÊé•Â§±Ë¥•:', error)
  }
})

onUnmounted(() => {
  // ÁßªÈô§WebSocketÁõëÂê¨Âô®
  websocketManager.off('context_status_update', handleContextWebSocketUpdate)
  console.log('ContextViewer unmounted')
})
</script>